/**
**
**  Poonez PWA Main Stylesheet
**
**/

:root {
    --mainColor: rgb(250, 56, 64);
}

* {
    box-sizing: border-box;
}

body {
    background: #f9f9f9;
    font-family: yekan, sans-serif, tahoma;
    direction: rtl;
    overflow-x: hidden;
}

body::before {
    width: 100%;
    height: 450px;
    background: -webkit-linear-gradient(270deg, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

a,
a:visited,
a:hover,
a:focus,
a:active {
    color: inherit;
    text-decoration: none;
}

#root {
    overflow-x: hidden;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

.container {
    width: 90%;
}

.container>div {
    padding-bottom: 50px;
}

/**
**
**  Page Loading
**
**/

#loading {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    z-index: 1000;
}

.loading-wrapper {
    transition: all 0.5s;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    background: #FFF;
    content: '';
    display: block;
}

.fadeOut {
    opacity: 0;
}

/**
**
** AppNav
**
**/

.App-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 80px;
    padding: 0 50px;
    text-align: center;
    z-index: 100000;
    position: relative;
}

.App-header a {
    font-size: 24px;
    position: absolute;
    top: 25px;
    right: 50px;
}

/**
**
**  Homepage Slider Section
**
**/

.sliderWrapper {
    width: 100vw;
    height: 200px;
    border-radius: 5px;
    overflow: hidden;
    margin-right: -10%;
}

.sliderInner {
    width: 100%;
    text-align: center;
    white-space: nowrap;
    overflow: auto;
}

.slideWrapper {
    width: 80%;
    height: 200px;
    overflow: hidden;
    display: inline-block;
    border-radius: 5px;
    text-align: center;
    margin-right: 5%;
    margin-left: 2.5%;
    position: relative;

}

.sliderWrapper .sliderInner .slideWrapper:first-child {
    margin-right: 10%;
}

.sliderWrapper .sliderInner .slideWrapper:last-child {
    margin-left: 10%;
}

.slideWrapper img {
    max-height: 100%;
    margin: auto;
    position: absolute;
    right: -9999px;
    left: -9999px;
    bottom: -9999px;
    top: -9999px;
    margin: auto;
}

/**
**
** Homepage Tags Section
**
**/

.tags-wrapper {
    margin: 10px 0;
    width: 100vw;
    margin-right: -8.5vw;
    height: 100px;
    overflow-y: hidden;
}

.tags-wrapper h4 {
    font-size: 18px;
    font-weight: bold;
    margin: 0;
    margin-right: 10%;
}

.tags-wrapper ul {
    list-style: none;
    white-space: nowrap;
    padding: 25px;
    padding-right: 10%;
    overflow: auto;
    width: 100vw;
    position: relative;
    margin: 0;
    padding-bottom: 50px;
}

.tags-wrapper li {
    display: inline-block;
    margin-left: 15px;
}

/**
**
** Tags
**
**/

.tag {
    padding: 5px 20px;
    background: var(--mainColor);
    border-radius: 50px;
    box-shadow: 0px 5px 15px 5px rgba(250, 56, 64, .3);
    color: #FFF !important;
}

/**
**
**  Homepage Search Section
**
**/

.search-wrapper {
    width: 100%;
    height: 80px;
    line-height: 60px;
    position: relative;
    margin-top: 25px;
}

.search-wrapper input {
    width: 100%;
    border-radius: 50px;
    border: none;
    padding: 0 25px;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.05);
}

.search-wrapper input:focus {
    outline: none;
}

.search-wrapper button {
    position: absolute;
    top: 0;
    bottom: 10px;
    left: 15px;
    margin: auto;
    z-index: 10;
    border: none;
    background: transparent;
}

.search-wrapper i {
    font-size: 28px;
    color: var(--mainColor);
}

/**
**
**  Homepage Nearby Section
**
**/

.nearby-wrapper {
    width: 100%;
    height: 80px;
}

.nearby-wrapper a {
    border-radius: 50px;
    background: #fff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
    color: #000;
    text-align: center;
    width: 100%;
    display: block;
    padding: 10px 0;
    position: relative;
    font-weight: bold;
}

.nearby-wrapper i {
    color: var(--mainColor);
    position: absolute;
    display: block;
    left: 25px;
    font-size: 24px;
    top: 10px;
}

/**
**
**  Homepage Categories Section
**
**/

.categories-wrapper {
    width: 100%;
    height: auto;
    /* background: #FFF; */
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    /* padding: 10px; */
    flex: 1;
    flex-wrap: wrap;
}

.categories-wrapper h3 {
    font-size: 18px;
    font-weight: bold;
    display: block;
    width: 100%;
    margin-bottom: 25px;
    padding-right: 10px;
}

.category-wrapper {
    width: calc(33.33333% - 20px);
    height: 130px;
    overflow: hidden;
    text-align: center;
    background: #FFF;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
    margin: 0 10px;
    padding: 15px;
    margin-bottom: 25px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.category-wrapper:nth-child(4n) {
    border-left: none;
}

.category-wrapper img {
    height: 30px;

}

.category-wrapper h4 {
    font-size: 14px;
    font-weight: 400;
    margin-top: 20px;
    color: #444;
}

/** 
**
** Slick Slider CSS File 
**
**/

.slick-list,
.slick-slider,
.slick-track {
    position: relative;
    display: block
}

.slick-loading .slick-slide,
.slick-loading .slick-track {
    visibility: hidden
}

.slick-slider {
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent
}

.slick-list {
    overflow: hidden;
    margin: 0;
    padding: 0
}

.slick-list:focus {
    outline: 0
}

.slick-list.dragging {
    cursor: pointer;
    cursor: hand
}

.slick-slider .slick-list,
.slick-slider .slick-track {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0)
}

.slick-track {
    top: 0;
    left: 0
}

.slick-track:after,
.slick-track:before {
    display: table;
    content: ''
}

.slick-track:after {
    clear: both
}

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px
}

[dir=rtl] .slick-slide {
    float: right
}

.slick-slide img {
    display: block
}

.slick-slide.slick-loading img {
    display: none
}

.slick-slide.dragging img {
    pointer-events: none
}

.slick-initialized .slick-slide {
    display: block
}

.slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent
}

.slick-arrow.slick-hidden {
    display: none
}

.slick-dots,
.slick-next,
.slick-prev {
    position: absolute;
    display: block;
    padding: 0
}

.slick-dots li button:before,
.slick-next:before,
.slick-prev:before {
    font-family: slick;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.slick-loading .slick-list {
    background: url(assets/ajax-loader.gif) center center no-repeat #fff
}

@font-face {
    font-family: slick;
    font-weight: 400;
    font-style: normal;
    src: url(assets/slick.eot);
    src: url(assets/slick.eot?#iefix) format('embedded-opentype'), url(assets/slick.woff) format('woff'), url(assets/slick.ttf) format('truetype'), url(assets/slick.svg#slick) format('svg')
}

.slick-next,
.slick-prev {
    font-size: 0;
    line-height: 0;
    top: 50%;
    width: 20px;
    height: 20px;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: 0;
    background: 0 0
}

.slick-next:focus,
.slick-next:hover,
.slick-prev:focus,
.slick-prev:hover {
    color: transparent;
    outline: 0;
    background: 0 0
}

.slick-next:focus:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-prev:hover:before {
    opacity: 1
}

.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
    opacity: .25
}

.slick-next:before,
.slick-prev:before {
    font-size: 20px;
    line-height: 1;
    opacity: .75;
    color: #fff
}

.slick-prev {
    left: -25px
}

[dir=rtl] .slick-prev {
    right: -25px;
    left: auto
}

.slick-prev:before {
    content: 'â†'
}

.slick-next:before,
[dir=rtl] .slick-prev:before {
    content: 'â†’'
}

.slick-next {
    right: -25px
}

[dir=rtl] .slick-next {
    right: auto;
    left: -25px
}

[dir=rtl] .slick-next:before {
    content: 'â†'
}

.slick-dotted.slick-slider {
    margin-bottom: 30px
}

.slick-dots {
    bottom: -25px;
    width: 100%;
    margin: 0;
    list-style: none;
    text-align: center
}

.slick-dots li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: 0;
    background: 0 0
}

.slick-dots li button:focus,
.slick-dots li button:hover {
    outline: 0
}

.slick-dots li button:focus:before,
.slick-dots li button:hover:before {
    opacity: 1
}

.slick-dots li button:before {
    font-size: 6px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: 'â€¢';
    text-align: center;
    opacity: .25;
    color: #000
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #000
}

.visible {
    opacity: 1 !important;
    transition: all 0.5s;
}

.switch-wrapper {
    position: relative;
    height: calc(100vh - 50px);
    width: 100vw;
}

.switch-wrapper>div {
    position: absolute;

}